.file {
  text-align: center;
}
.message {
  padding: 10px;
  margin: 10px;
  width: fit-content;
  background-color: lightgray;
  border-radius: 5px;
}
.message__user {
  margin-left: auto;
  color: rgb(255, 253, 253);
  text-align: right !important;
  border: none;
  border-radius: 5px;
  background-color: rgb(46, 46, 209) !important;
}
.message__userCard {
  background-color: rgb(46, 46, 209) !important;
}
.message__guestCard {
  background-color: lightgray !important;
}
.app__form {
  padding: 20px;
  position: fixed;
  bottom: 0;
  z-index: 1;
  background-color: #e9e9e9;
  width: 100%;
  margin: 18px;
}
.app__control {
  display: flex !important;
  flex-direction: row !important;
}
.app__iconButton {
  flex: 0;
}
.app__input {
  flex: 0.96;
}
